import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Email from "../components/SendEmail.vue"
import SpaceX from "../views/SpaceXWork.vue"
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  }, {
    path: "/SpaceXAPI",
    name: "SpaceXAPI",
    component: SpaceX
  },
  {
    path: "/Email",
    name: "Email",
    component: Email
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
