<template>
  <v-card class="mx-auto" id="main-card" max-width="100%" :height="openedPanel == null?'25%':'50%'" outlined
    elevation="0">
    <div id="education" name='one'>
      <h3>
        <v-icon>mdi-image-multiple</v-icon> 作品集
      </h3>
      <v-divider light></v-divider>
      <!--分類選擇-桌面版本-->
      <div class="classify" v-show="$RWD.GetDeviceType()=='desktop'">
        <v-list dense rounded>
          <v-subheader>分類</v-subheader>
          <v-list-item-group v-model="selectedItem" color="primary">
            <v-list-item v-for="(item, i) in classify" :key="i">
              <v-list-item-content>
                <v-list-item-title v-text="item.text"></v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-item-group>
        </v-list>
      </div>
      <!--分類選擇-Mobile版本-->
      <el-tabs v-model="selectedItem" v-show="$RWD.GetDeviceType()!='desktop'">
        <el-tab-pane label="WinForm APP" name="0"></el-tab-pane>
        <el-tab-pane label="網頁設計" name="1"></el-tab-pane>
      </el-tabs>

      <!--作品內容-->
      <div class="content">
        <v-expansion-panels popout focusable v-model="openedPanel">
          <v-expansion-panel v-for="item in showCollection" :key="item.id" :click="href='#one'">
            <v-expansion-panel-header><strong>{{item.title}}</strong>
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <!--
              <v-btn x-small @click=" imagZoom">放大檢視</v-btn>
            <v-img max-height="100" max-width='100%' v-bind:src=item.imgUrl
              lazy-src=" https://picsum.photos/id/11/100/60">
              <template v-slot:placeholder>
                <v-row class="fill-height ma-0" align="center" justify="center">
                  <v-progress-circular indeterminate color="blue lighten-2"></v-progress-circular>
                </v-row>
              </template>
            </v-img>
            -->
              <v-carousel cycle height="460" hide-delimiter-background show-arrows-on-hover>
                <v-carousel-item v-for="(slide, i) in item.imgUrls" :key="i">
                  <v-sheet height="100%">
                    <v-row class="fill-height" align="center" justify="center">
                      <div class="display-3">
                        <v-btn x-small @click="imagZoom2(slide)">放大檢視</v-btn>
                        <v-img max-height="auto" max-width='600px' v-bind:src=slide>
                        </v-img>
                      </div>
                    </v-row>
                  </v-sheet>
                </v-carousel-item>
              </v-carousel>
              <el-divider></el-divider>
              <v-icon>mdi-alpha-t-circle-outline</v-icon> <strong>Framework/語言</strong>
              <p>
                <v-chip small v-for="lang in item.lang" :key=lang class="ma-2" color='red' text-color="white">
                  {{lang}}
                </v-chip>
              </p>
              <v-icon>mdi-alpha-d-circle-outline</v-icon> <strong>詳細說明</strong>
              <p v-for="(des) in item.tech_description" :key="des">
                {{des}}
              </p>
              <el-divider></el-divider>
              <div class="contentItem">
                <v-icon>mdi-text-box-outline</v-icon> <b>說明</b>
                <p v-text="item.content"></p>
              </div>
              <div class="contentItem">
                <v-icon>mdi-text-box-outline</v-icon> <b>作品連結</b>

                <li v-if="item.workLink.type=='link'"> <a :href=item.workLink>{{item.workLink.url}}</a></li>
                <li v-if="item.workLink.type=='route'">
                  <v-btn :disabled="item.workLink.route==''" samll @click="go_to_work_page(item.workLink.route)"> 檢視作品
                  </v-btn>
                </li>
              </div>
              <div class="contentItem">
                <v-icon>mdi-text-box-outline</v-icon> <b>相關連結</b>

                <a v-for="link in item.links" :key="link" :href="link">
                  <li>{{link}} </li>
                </a>
              </div>

            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </div>

      <!--作品圖片放大檢視-->
      <el-dialog title="作品檢視" :visible.sync="dialogVisible" width="100%" :before-close=ZoomClose>
        <v-btn color="error" fab large dark @click="ZoomClose">關閉</v-btn>
        <el-divider></el-divider>
        <v-img max-height="100%" max-width="100%" v-bind:src=selectedImgUrl
          lazy-src="https://picsum.photos/id/11/100/60">
          <template v-slot:placeholder>
            <v-row class="fill-height ma-0" align="center" justify="center">
              <v-progress-circular indeterminate color="blue lighten-2"></v-progress-circular>
            </v-row>
          </template>
        </v-img>

      </el-dialog>
    </div>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    openedPanel: "null",
    dialogVisible: false,
    selectedImgUrl: "",
    selectedItem: 0,
    tabActive: "",
    open: false,
    classify: [
      //分類集合
      { id: 0, text: "WinForm APP", icon: "mdi-clock" },
      { id: 1, text: "網頁設計", icon: "mdi-account" },
    ],
    Collections: {
      //作品集合
      WinformAPP: [
        {
          title: "智慧診斷平台-Vibration Analysis",
          content:
            "以 .Netframework Winform 專案開發智慧診斷平台系統，該系統主要為收集機台關鍵零組件的振動數據，並進行演算法分析，以實現設備故障預警功能。目前在半導體業、面板業、工具機業等領域皆有實際應用，提供設備預防保養的加值服務。",
          imgUrl: "img/collection/order_system.png",
          imgUrls: [
            "img/collection/IDMS-1.png",
            "img/collection/IDMS-2.png",
            "img/collection/IDMS-3.png",
            "img/collection/IDMS-4.png",
            "img/collection/IDMS-5.png",
          ],
          lang: [".Net Framework", "C#"],
          tech_description: [
            "- 系統採多執行續架構，大量使用 async/Task 技術並行處理多個感測器的數據交握與訊號後處理(演算法)。",
            "- 使用 TCP/IP Socket Client/Server 與感測器連線進行量測值讀取",
            "- 使用 TCP/IP Socket Client/Server 建立控制系統重啟、關閉、等各項操作的API。",
            "- 使用 UDP/IP Socket Client/Server 進行消息發布。",
            "- 使用 MQTTnet 套件建立數據訂閱功能，使使用者可以方便的取得感測器的各項數據。",
            "- 使用 WebSocket 技術將即時性數據上拋到網頁。",
            "- 使用 .Net.Http 使用GET/POST 後端API上拋數據到後端。",
            "- 整合 PostgreSQL 儲存感測器數據以及訊號特徵。",
            "- 使用 MSChart 呈現感測器即時數據與特徵數據。",
            "- 使用 iTextSharp 套件，根據警報規則產生對應的報表並上傳到後端儲存。",
          ],
          workLink: {
            type: "route",
            route: "",
            url: "",
          },
        },
        {
          title: "PLC Data Getway",
          content:
            "以 .Netframework Winform 專案開發PLC Data Getway系統，讀取客戶指定的PLC記憶體數據並進行儲存與Chart顯示。並整合智慧診斷平台數據，上報整合後的報表資料至客戶指定的網路資料夾。",
          imgUrl: "img/collection/order_system.png",
          imgUrls: [
            "img/collection/data_gateway/1.png",
            "img/collection/data_gateway/2.png",
            "img/collection/data_gateway/3.png",
            "img/collection/data_gateway/4.png",
          ],
          lang: [".Net Framework", "C#"],
          tech_description: [
            "- 系統採多執行續架構，大量使用 async/Task 技術並行處理與多部PLC的連線與數據讀取，以及處理智慧診斷平台發布數據(MQTT)",
            "- 使用 MC Protocol(網路介面>3E) 與PLC進行數據交換。",
            "- 使用 TCP/IP Socket Client/Server 與智慧診斷平台進行通訊，操作智慧診斷平台。",
            "- 使用 UDP/IP 讀取智慧診斷平台發布的各項消息。",
            "- 使用 MQTTnet 套件訂閱智慧診斷平台發布的數據，與PLC數據整合。",
            "- 使用 Microsoft.Office.Interop.Excel 組件自動產生EXCEL報表。",
            "- 使用 .Net.Http 使用GET/POST 後端API上拋數據到後端。",
            "- 使用 MSChart 呈現PLC數據(如機械手臂各軸減速機的電流值)。",
            "- 利用 FileSystemWatcher，監控指定的網路資料夾，並讀取特定的更新檔並進行解壓縮、檔案複製，實現遠端系統更新功能。",
          ],
          workLink: {
            type: "route",
            route: "",
            url: "",
          },
        },
        {
          title: "自助取票系統",
          content:
            "使用Winform開發自助取票系統，於政府舉辦的展覽中供使用者使用。",
          imgUrl: "img/collection/order_system.png",
          imgUrls: [
            "img/collection/order_system-1.png",
            "img/collection/order_system-2.png",
            "img/collection/order_system-3.png",
            "img/collection/order_system-4.png",
          ],
          lang: [".Net Framework", "C#"],
          tech_description: "",
          workLink: {
            type: "route",
            route: "",
            url: "",
          },
        },
        {
          title: "地政資訊整合服務",
          content:
            "串接'地政整合資訊服務共享協作平台'，可使房仲業者快速、簡易地進行資料查詢；查詢過的資料儲存於資料庫，可節省查詢成本。",
          imgUrl: "img/collection/landService.png",
          imgUrls: [
            "img/collection/landService-1.png",
            "img/collection/landService-2.png",
            "img/collection/landService-3.png",
          ],
          lang: [".Net Framework", "C#"],
          tech_description: "",
          links: ["https://cop.land.moi.gov.tw/caseshow/casedata.aspx"],
          workLink: {
            type: "route",
            route: "",
            url: "",
          },
        },
      ],
      Website: [
        {
          title: "個人履歷(This website)",
          content: "使用 Vue.js 前端框架建構個人履歷與作品及呈現。",
          imgUrl: "img/collection/personalResume.png",
          imgUrls: ["img/collection/personalResume.png"],
          lang: ["Vue.js", "HTML", "CSS", "JavaScript"],
          tech_description: ["-", "-"],
          workLink: {
            type: "link",
            url: "https://ginwei-resume-app.herokuapp.com",
          },
          links: ["https://github.com/ss610349/resume-front-end.git"],
        },
        {
          title: "SpaceX 發射倒數計時器",
          content:
            "使用SpaceX提供的API，針對最近要準備發射的火箭之發射時間，製作倒數計時器，並提供Youtube直播串流影片觀看。",
          imgUrl: "img/collection/spaceX_api_call_1.png",
          imgUrls: ["img/collection/spaceX_api_call_1.png"],
          lang: ["Vue.js", "HTML", "CSS", "JavaScript"],
          tech_description: "",
          workLink: {
            type: "route",
            route: "/SpaceXAPI",
            url: "https://ginwei-resume-app.herokuapp.com/SpaceXAPI",
          },
          links: [
            "https://docs.spacexdata.com/",
            "https://github.com/r-spacex/SpaceX-API",
          ],
        },
        {
          title: "預防診斷系統網站",
          content:
            "將原為Winform版本之系統改寫圍網頁板，使用Vue.js前端框架，並應用Websock、WebAPI等通訊方式與後端溝通。",
          imgUrl: "img/collection/idms_home.png",
          imgUrls: ["img/collection/idms_home.png"],
          lang: ["Vue.js", "HTML", "CSS", "JavaScript"],
          tech_description: "",
          workLink: {
            type: "route",
            route: "",
            url: "",
          },
        },
      ],
    },
    showCollection: [],
  }),
  computed: {
    timeline_firt_col_span() {
      var device = this.$RWD.GetDeviceType();
      console.log(device);
      const root = document.documentElement;
      root.style.setProperty(
        "--caption_margin_lef",
        `${device == "desktop" ? "-45" : "-17"}px`
      );
      return device == "desktop" ? "3" : "6";
    },
  },
  watch: {
    selectedItem: {
      handler: function (n, o) {
        console.log(n, o);
        if (n == undefined) return;
        this.openedPanel = null;
        this.showCollection =
          n == 0 ? this.Collections.WinformAPP : this.Collections.Website;
      },
    },
  },
  methods: {
    go_to_work_page(route) {
      this.$router.push(route);
    },
    HeadClick(url) {
      this.selectedImgUrl = url;
    },
    Classify_item_click() {
      console.log(this.ClassifySelect);
    },
    imagZoom() {
      if (this.selectedImgUrl == "") return;
      this.dialogVisible = true;
    },
    imagZoom2(url) {
      this.selectedImgUrl = url;
      console.log(this.selectedImgUrl);
      this.dialogVisible = true;
    },
    ZoomClose() {
      this.dialogVisible = false;
    },
  },
  mounted() {
    this.showCollection = this.Collections.WinformAPP;
    var device = this.$RWD.GetDeviceType();
    const root = document.documentElement;
    root.style.setProperty(
      "--contentWidth",
      `${device == "desktop" ? "80" : "100"}%`
    );
    root.style.setProperty(
      "--contentPaddingLeft",
      `${device == "desktop" ? "20" : "0"}px`
    );
  },
};
</script>
<style scoped>
#main-card {
  margin-bottom: 2px;
  height: 400px;
  width: auto;
}
#education {
  background-color: inherit;
  float: left;
  text-align: ntent;
  width: 90%;
  margin-left: 20px;
  height: 100%;
}

.el-tag {
  margin-bottom: 2px;
}
.v-divider {
  margin-left: 2px;
  margin-right: 2px;
  margin-bottom: 10px;
}
h3 {
  margin-top: 10px;
  margin-bottom: 10px;
}

.v-timeline {
  margin-left: -30px;
}

.pt-1 {
  margin-left: -40px;
  font-size: 13px;
}
:root {
  --caption_margin_lef: -1px;
}
.caption {
  margin-left: var(--caption_margin_lef);
}

.classify {
  float: left;
  width: 20%;
  padding-right: 40px;
  margin-right: -14px;
  z-index: 0;
}
.v-list-item {
  padding-right: 0px;
}
:root {
  --contentWidth: "100%";
  --contentHeight: "100%";
  --contentPaddingLeft: "0px";
}
.content {
  float: left;
  width: var(--contentWidth);
  height: var(--contentHeight);
  margin-left: 0px;
  padding-left: var(contentPaddingLeft);
  z-index: 0;
}

p,
li {
  margin-left: 26px;
}

.contentItem {
  margin-bottom: 20px;
}
</style>