let axios = require("axios");
const nextLaunch = {
    GetData: async function () {
        try {
            var data = null;
            var config = {
                method: "get",
                url: "https://api.spacexdata.com/v4/launches/upcoming",
                headers: {},
            };
            await axios(config)
                .then(function (response) {
                    data = response.data;
                })
                .catch(function (error) {
                    console.log(error);
                });
            return data;
        } catch (error) {
            return null;
        }

    }
}

export default nextLaunch;