<template>
  <div id="app">
    <v-app id="inspire">
      <v-container class="scroll-y">
        <v-btn id="sendMailBtn" v-scroll="onScroll" small v-show="$router.currentRoute.path != '/Email'" fab primary
          fixed top left color="danger" @click="sendEmail">
          <v-icon color="red">mdi-email</v-icon>
        </v-btn>
        <v-btn id="sendMessageBtn" v-scroll="onScroll" small v-show="$router.currentRoute.path != '/Email'" fab primary
          fixed top left color="danger" @click="sendEmail">
          <v-icon color="blue">mdi-message</v-icon>
        </v-btn>
        <v-btn v-scroll="onScroll" v-show="fab" fab dark fixed bottom right color="primary" @click="toTop">
          <v-icon>mdi-chevron-up</v-icon>
        </v-btn>
        <v-main>
          <router-view></router-view>
        </v-main>
      </v-container>
    </v-app>
  </div>

</template>

<script>
export default {
  name: "App",
  data: () => ({
    fab: false,
    sendmail: false,
    //
  }),
  methods: {
    onScroll(e) {
      if (typeof window === "undefined") return;
      const top = window.pageYOffset || e.target.scrollTop || 0;
      this.fab = top > 20;
    },
    toTop() {
      this.$vuetify.goTo(0);
    },
    sendEmail() {
      if (this.$router.currentRoute.path != "/Email") {
        console.log("someone wanna send mail to me !");
        this.$router.push("/Email");
        this.sendmail = true;
      }
    },
  },
  mounted() {
    console.log(this.$RWD.GetDeviceType());
  },
};
</script>
