<template>
  <div id="email" v-loading="loading">
    <div class="back">
      <v-btn @click="back" color="primary">Back</v-btn>
    </div>
    <div class="content">
      <div id="des">
        <v-icon x-large>mdi-email</v-icon> Write something to me!
        <p>請留下您的聯絡方式，方便日後聯絡您.</p>
      </div>

      <el-divider></el-divider>

      <form class="contact-form" @submit.prevent="sendEmail">
        <label>Your Name</label>
        <el-input v-model="form.user_name" name="user_name"></el-input>
        <label>Your Email</label>
        <el-input v-model="form.user_mail" name="user_mail"></el-input>
        <label>Message</label>
        <el-input type="textarea" :rows="12" v-model="form.message" name="message"></el-input>
        <v-btn type="submit" block> SEND</v-btn>
      </form>

    </div>
  </div>
</template>
<script>
import emailjs from "emailjs-com";
export default {
  data: () => ({
    loading: false,
    form: {
      user_name: "",
      user_mail: "",
      message: "",
    },
  }),
  methods: {
    back() {
      this.$router.push("/");
    },
    sendEmail(e) {
      this.loading = true;
      emailjs
        .sendForm(
          "service_tze5ryz",
          "template_gwue97n",
          e.target,
          "user_wfZbsMsxrvrqscRDp0opz"
        )
        .then(
          (result) => {
            console.log("SUCCESS!", result.status, result.text);
            this.showOK();
          },
          (error) => {
            console.log("FAILED...", error);
          }
        );
      console.log("OK");
    },
    async showOK() {
      this.loading = false;
      this.$swal({
        text: "MAIL寄送成功!",
        confirmButtonText: "OK",
      }).then((res) => {
        console.log(res);
        this.$router.push("/");
      });
    },
  },
};
</script>

<style>
#email {
  background-color: white;
}
#des {
  margin-top: 20px;
  margin-bottom: -10px;
  font-size: 25px;
}
#des p {
  font-size: 15px;
}
</style>