const RWD = {
    windowWidth: 0,
    windowHeight: 0,
    GetDeviceType: function () {
        this.windowHeight = window.innerHeight;
        this.windowWidth = window.innerWidth;
        if (this.windowWidth > 800)
            return 'desktop';
        if (this.windowWidth < 800 && this.windowWidth > 400)
            return 'pad';
        else
            return 'mobile';
    }

}

export default RWD;