<template>
  <div id="chartRoom" :v-loading="loading">
    <div id="message_show" ref="msgContainer">
      {{message_rev}}
      <div v-bind:class="msgBox_style" v-for="obj in message_list" :key="obj.time_stamp">
        <v-timeline :reverse="!obj.IsfromOthers" dense>
          <v-timeline-item :color="obj.IsfromOthers? 'red lighten-3':'primary'" small :hide-dot="!obj.IsfromOthers"
            :dark="!obj.IsfromOthers" right>
            <span slot="opposite">Tus eu perfecto</span>
            <v-card class="elevation-2">
              <v-card-title class="headline">
                {{obj.id}}
              </v-card-title>
              <v-card-text id="msg_content">
                {{obj.message}}
              </v-card-text>
            </v-card>
          </v-timeline-item>
        </v-timeline>
      </div>

    </div>
    <el-row :gutter="20">
      <el-col :span="18">
        <el-input id="msgInput" @keyup.enter.native="SendMessage" v-model="messageToSend" type="text">
        </el-input>
      </el-col>
      <el-col :span="2">
        <v-btn @click="SendMessage">Send</v-btn>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import { websocket_obj, ws } from "../../plugins/websocket-api.js";
export default {
  data() {
    return {
      loading: false,
      count: 5,
      message_list: [],
      messageToSend: "",
      message_rev: "",
    };
  },
  methods: {
    OnMessage: function (msg) {
      console.log(msg);
      if (msg.data == "OK") return;
      var msg_obj = JSON.parse(msg.data);
      this.message_list.push(msg_obj);
    },
    SendMessage() {
      if (this.messageToSend == "") return;
      let msg_obj = {};
      msg_obj.SendTo = "ALL";
      msg_obj.message = this.messageToSend;
      msg_obj.id = "";
      msg_obj.IsfromOthers = false;
      msg_obj.time_stamp = Date.now();

      try {
        ws.send(JSON.stringify(msg_obj));
        this.messageToSend = "";
        this.getFocus("msgInput");
        this.inputLossfocus = false;
        this.message_list.push(msg_obj);
      } catch (error) {
        console.log(error);
      }
    },
    getFocus(id) {
      document.getElementById(id).focus();
    },
  },
  mounted() {
    console.log("is dev:" + this.$isDev());
    websocket_obj.ConnectToServe(
      this.$isDev() ? "ws://localhost:3000" : "wss://localhost:3000"
    );
    this.loading = ws.readyState != WebSocket.OPEN;
    ws.onmessage = (msg) => {
      this.OnMessage(msg);
    };
  },
  computed: {
    msgBox_style: () =>
      "margin-bottom:22px;text-align:right;width:50%;float:left;margin-top:10px",
  },
  watch: {
    message_list: function () {
      this.$nextTick(function () {
        var container = this.$refs.msgContainer;
        container.scrollTop = container.scrollHeight + 120;
      });
    },
  },
};
</script>

<style scoped>
#message_show {
  background: black;
  color: white;
  height: 400px;
  overflow: scroll;
}
v-timeline-item {
  width: 70%;
}
.scrollable {
  overflow: hidden;
  overflow-y: scroll;
  height: calc(100vh - 20px);
}
#msg_content {
  font-size: 20px;
}
input el-input {
  font-size: initial;
}
</style>