<template>
  <div>
    <v-main>
      <div class="banner"> <a href=" https://www.spacex.com/"> https://www.spacex.com/</a> </div>
      <v-carousel cycle height="500" hide-delimiter-background show-arrows-on-hover>
        <v-carousel-item v-for="(mission, i) in CommingLaunchMission" :key="i">
          <v-sheet color="black" height="100%">
            <div class="content">
              <div class="starlink">
                {{ mission.name.includes('Starlink')?"星鏈計畫":""  }}
                <p>{{mission.name}}</p>
              </div>
              <div class="imageShow">
                <v-img max-height="90%" max-width="80%" v-bind:src=mission.links.patch.small>
                  <template v-slot:placeholder>
                    <v-row class="fill-height ma-0" align="center" justify="center">
                      <v-progress-circular indeterminate color="blue lighten-2"></v-progress-circular>
                    </v-row>
                  </template>
                </v-img>
              </div>
              <div class="Describe">
                <div id="launchTime"> LAUNCH TIME <br /><span> {{ mission.date_utc.substring(0,19) }}</span> </div>
                <div id=details> {{mission.details}}</div>
              </div>
            </div>
            {{mission.name}}
          </v-sheet>
        </v-carousel-item>
      </v-carousel>
      <div class="NextLaunch" ref="n_launch">
        <v-divider dark></v-divider>
        <h1>
          <v-icon color="white" large>mdi-rocket-launch-outline</v-icon> NEXT LAUNCH
        </h1>
        <div id="Countup">
          <v-icon color="white" x-large>mdi-timer-outline</v-icon> {{countupTime}}
        </div>
        <v-img v-show="false" max-height="90%" max-width="80%" v-bind:src=NearestMission.links.patch.large>
          <template v-slot:placeholder>
            <v-row class="fill-height ma-0" align="center" justify="center">
              <v-progress-circular indeterminate color="blue lighten-2"></v-progress-circular>
            </v-row>
          </template>
        </v-img>
        <div class="Describe2">
          <div id="launchTime2"> LAUNCH TIME <span> {{ NearestMission.date_utc.substring(0,19) }}</span> </div>
          <div id="launchTime2"> Mission Name <span> {{ NearestMission.name }}</span> </div>
          <div id=details2> {{NearestMission.details}}</div>
          <v-divider dark></v-divider>
        </div>

        <h1>
          <v-icon color="white" large>mdi-video</v-icon> LIVE SHOW
        </h1>
        <div id="youtubeShow">
          <p style="color:red" v-if=show_video_notice> Launching not yet started, Now Playing : 'Starship | SN10 |
            High-Altitude Flight
            Test'(2021/3/4) </p>
          <myyoutube :Width="$RWD.windowWidth/2" :videoId="youtube_id" :Name="''"></myyoutube>
        </div>

        <div id="chartRoom" hidden>
          <h1>
            <v-icon color="white" large>mdi-chat-processing-outline</v-icon> CHAT ROOM
          </h1>
          <chartRoom />
        </div>
      </div>

    </v-main>

    <!--
    <v-btn @click="getData()">Update!</v-btn>
    -->
  </div>
</template>

<script>
import nextLaunch from "../../plugins/space_apis.js";
import myyoutube from "../my_youtube.vue";
import chartRoom from "../Works/chatRoom.vue";
export default {
  components: { myyoutube, chartRoom },
  data() {
    return {
      inputLossfocus: false,
      colors: ["indigo", "warning"],
      CommingLaunchMission: [],
      countupTime: "",
      playerVars: {
        autoplay: 0,
      },
    };
  },
  methods: {
    async getData(mission_num) {
      try {
        this.CommingLaunchMission = [];
        var all_mission = await nextLaunch.GetData();
        if (all_mission == null) return;
        all_mission.forEach((mission) => {
          if (this.CommingLaunchMission.length == mission_num) return;
          var time = Date.parse(mission.date_utc).valueOf();
          if (time > Date.now()) this.CommingLaunchMission.push(mission);
        });
      } catch (error) {
        console.logO("getdata error" + error);
      }
    },
    countup() {
      if (this.CommingLaunchMission.length == 0) return;
      var aim_dateTime = Date.parse(
        this.CommingLaunchMission[0].date_utc
      ).valueOf();
      let ticks = aim_dateTime - Date.now();
      //get seconds from ticks
      var ts = ticks / 1000;
      //conversion based on seconds
      var hh = Math.floor(ts / 3600);
      var mm = Math.floor((ts % 3600) / 60);
      var ss = (ts % 3600) % 60;
      //prepend '0' when needed
      hh = hh < 10 ? "0" + hh : hh;
      mm = mm < 10 ? "0" + mm : mm;
      ss = ss < 10 ? "0" + ss : ss;
      var str = hh + ":" + mm + ":" + ss;
      this.countupTime = str.substring(0, 14);
      return 1;
    },
  },
  watch: {
    CommingLaunchMission: {
      handler: function (n, o) {
        if (n !== o) {
          setInterval(() => this.countup(), 200);
        }
      },
    },
  },
  computed: {
    NearestMission() {
      if (this.CommingLaunchMission.length == 0) return null;
      console.log(this.CommingLaunchMission[0]);
      return this.CommingLaunchMission[0];
    },
    youtube_id() {
      if (this.NearestMission == null) return "ODY6JWzS8WU";
      if (this.NearestMission.links == null) return "ODY6JWzS8WU";
      if (this.NearestMission.links.youtube_id == null) return "ODY6JWzS8WU";
      else {
        return this.NearestMission.links.youtube_id;
      }
    },
    show_video_notice() {
      try {
        return this.NearestMission.links.youtube_id == null;
      } catch (error) {
        return true;
      }
    },
  },
  mounted() {
    this.getData(8);
    var device = this.$RWD.GetDeviceType();
    const root = document.documentElement;
    root.style.setProperty(
      "--img_margin_left",
      `${device == "desktop" ? this.$RWD.windowWidth / 2 - 180 : "20"}px`
    );
  },
};
</script>

<style scoped>
#chartRoom {
  background-color: black;
  color: white;
  height: 300px;
}
.content {
  padding-top: 20px;
  width: 100%;
  height: 100%;
  background-color: black;
  background-image: url("https://cnet2.cbsistatic.com/img/rFBSx0wBzu92Zo34VSai6x3PuwA=/940x0/2020/10/02/5564064a-dff2-4acf-a0cd-1c4f0c636b62/spacexspaceforceoct2020.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.banner {
  background-color: rgb(31, 31, 31);
  color: white;
  text-align: right;
  height: 25px;
}

.banner a {
  color: rgb(248, 161, 32);
  text-decoration: none;
}

.display-2 {
  font-size: 10px;
}

.starlink {
  font-size: 40px;
  text-align: right;
  margin-right: 20px;
  margin-top: -10px;
}
.starlink p {
  font-size: 16px;
  margin-top: -13px;
  color: silver;
}

.Describe {
  float: left;
  width: 40%;
}

.imageShow {
  float: left;
  width: 40%;
  margin-right: -50px;
}

.imageShow v-img {
  margin-bottom: 100px;
  background-color: red;
}

#launchTime {
  font-size: 30px;
  width: 220%;
}
#details {
  width: 150%;
}
#launchTime span {
  font-size: 50px;
  margin-left: 10px;
  color: rgb(248, 161, 32);
}

.NextLaunch {
  padding-top: 30px;
  background-color: rgb(0, 51, 51);
  color: white;
}
:root {
  --img_margin_left: 400px;
}
.NextLaunch .v-image {
  margin-left: var(--img_margin_left);
  text-align: center;
  width: 300px;
}

.Describe2 {
  text-align: center;
  margin-bottom: 40px;
}

#details2 {
  margin-bottom: 40px;
  padding-left: 60px;
  padding-right: 60px;
  font-style: italic;
}

#launchTime2 {
  font-size: 20px;
  color: rgb(190, 190, 190);
}
#launchTime2 span {
  font-size: 30px;
  margin-left: 20px;
  color: gold;
}

#Countup {
  font-size: 60px;
  text-align: center;
}

#youtubeShow {
  text-align: center;
  color: white;
  height: auto;
}

footer {
  background-color: black;
  height: 40px;
  padding: 0;
  color: white;
  text-align: center;
  font-size: 20px;
  padding-top: 5px;
}

.NextLaunch h1 {
  margin-left: 30px;
}
</style>